<template>
  <div class="recommendsongs">
      <!-- <a href="##">返回</a> -->
    <div class="top" >
      <img :src="gedan.coverImgUrl" class="img1">
      <img :src="gedan.coverImgUrl" class="imgbg">
        <div class="right">
          <div class="right1">{{gedan.name}}</div>
          <div class="right2">
            <img v-if="gedan.creator" :src="gedan.creator.avatarUrl" class="img2">
            <span v-if="gedan.creator">{{gedan.creator.nickname}}</span>
          </div>
        </div>
    <!-- <img :src="{{this.}}"> -->
    </div>
    <div class="biao">歌曲列表</div>

    <ul class="kong">
      <!-- 循环热歌榜列表 -->
      <img src="../assets/all.png" alt="">

      <li-item v-for="(item,index) in hotList" :item="item" :key="item.id" @play="$emit('play',$event)" :playMusic="playMusic">
        
        <template v-slot:sortnum>
            <b>{{((index+1) &lt; 10) ? '0'+(index+1):index+1}}</b>
        </template>
      </li-item>

    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import LiItem from '@/components/LiItem'

export default {
  name: 'RecommendSongs',
  data(){
    return{
      gedan:[],
      bigid:0,
      listInfo:null,
      hotList:[],
      allList:[],
      page:0,//记录页码
      perpage:20//每页显示20条
    }
  },
  props:{
      id:Number,playMusic:Object
  },
  components: {
    LiItem,
  },
  mounted(){
    window.addEventListener('scroll',this.debounce(this.handleScroll,1000))
  },
  beforeRountLeave(to,from,next){
    window.removeEventListener('scroll',this.debounce(this.handleScroll,1000));
    next();
  },
  methods:{
    debounce(fn, wait) {
      let timeout = null
      return function() {
        if(timeout !== null) clearTimeout(timeout)     
        timeout = setTimeout(fn, wait);
      }
    },
    handleScroll(){
      // console.log(document.documentElement.scrollTop);
      // console.log(document.documentElement.scrollHeight);
      // console.log(document.documentElement.clientHeight);
      let scrollA = document.documentElement.scrollHeight;
      let scrollC = document.documentElement.scrollTop;
      let scrollB = document.documentElement.clientHeight;
      let whnb = scrollA-(scrollC+scrollB);
      // console.log(whnb);
      if(whnb<3){
        // console.log('nb');
          this.page++; // 1  2
          let start=(this.page)*this.perpage;// 20   40
          let end=(this.page+1)*this.perpage;// 40   60
          this.hotList=this.hotList.concat(this.allList.slice(start,end));
          
        
      }
    }
  },
  created(){
    // if(id==){

    // }
  },
  beforeRouteEnter(to,from,next){
    next(vm=>{
      vm.axios.get('/playlist/detail?id='+vm.id).then(d=>{
        // console.log(d);
        vm.gedan = d.data.playlist;
        vm.allList=d.data.playlist.tracks.map((data,index)=>{
        data.song={
          privilege:d.data.privileges[index],
          artists:data.ar,
          album:data.al
        }
        return data;
      });
      // slice(0,20)=>1 slice(20,40)=>2 slice(40,60)=>3
      //每页显示 20
      vm.hotList=vm.allList.slice(0,20);
        console.log(vm.gedan);
      })

      // vm.bigid = to.params.item;
      // console.log(vm.bigid)
    })
  },

  // beforeRouteLeave(){
  //   this.$axios.get('/personalized?limit=100').then(d=>{
  //     console.log(d);
  //     // this.recommend_songs=d.data.result.slice(0,6);
  //   })
  // }
}
</script>

<style lang="less" scoped>
.biao{
  height: 4vh;
  line-height: 4vh;
  padding-left: 2vw;
  width: 100vw;
  font-size: 12px;
  color: #666;
  background-color: #eeeff0;

}
.recommendsongs{
  position: relative;
  height: 100%;
  margin-top: 80px;
  color: white;
  .top{
    width: 100vw;
    height: 28vh;
    // background: wheat;
    position: relative;
    .imgbg{
      position: absolute;
      top: 0;
      left: 0;
      width: 100vw;
      height: 28vh !important;
      z-index: -1;
      filter: blur(15px);
    }
    .img1{
      // width: 100vw;
      height: 16vh;
      width: 16vh;
      position: absolute;
      top: 6vh;
      left: 2vw;
    }

    .right{
      width: 48vw;
      position: absolute;
      top: 6vh;
      margin-left: 40vw;
      .right1{
        position: absolute;
        font-weight: bold;
        // top: 6vh;
      }
      .right2{
        width: 48vw;
        position: absolute;
        height: 30px;
        top: 10vh;
        .img2{
          // position: absolute;
          // display: inline-block;
          width: 30px;
          float: left;
          border-radius: 50%;

        }
        span{
          float: left;
          font-size: 14px;
          line-height: 30px ;

        }
        

      }

    }
  }
}
.kong{
  width: 100vw;
  position: relative;
  margin-bottom: 80px;
  img{
    position: absolute;
    opacity: 0.2;
    top: 0;
    left: 0;
    z-index: -3;

  }
}
</style>
